import React, { useState, useEffect, useRef } from 'react'
import { View, Text, Image } from '@tarojs/components'
import Taro from '@tarojs/taro'
import './index.scss'

interface CartItem {
  serviceId: string
  serviceName: string
  duration: number
  price: number
  discountPrice?: number
  date: string
  time: string
  therapistName: string
  therapistAvatar?: string
}

interface ShoppingCartProps {
  items: CartItem[]
  therapist?: any
  onCheckout: () => void
}

const ShoppingCart: React.FC<ShoppingCartProps> = ({ items, therapist, onCheckout }) => {
  const [isExpanded, setIsExpanded] = useState(false)
  const [countdown, setCountdown] = useState(180) // 3ÂàÜÈíü = 180Áßí
  const timerRef = useRef<any>(null)

  // ËÆ°ÁÆóÊÄª‰ª∑
  const totalOriginalPrice = items.reduce((sum, item) => sum + item.price, 0)
  const totalDiscountPrice = items.reduce((sum, item) => sum + (item.discountPrice || item.price), 0)
  const totalSavings = totalOriginalPrice - totalDiscountPrice
  const hasItems = items.length > 0

  // ÂÄíËÆ°Êó∂ÈÄªËæë
  useEffect(() => {
    if (hasItems && isExpanded) {
      timerRef.current = setInterval(() => {
        setCountdown((prev) => {
          if (prev <= 1) {
            clearInterval(timerRef.current)
            // Êó∂Èó¥Âà∞ÔºåÂèØ‰ª•Ëß¶ÂèëÈáçÁΩÆÈÄªËæë
            Taro.showToast({
              title: 'È¢ÑÁ∫¶Â∑≤Ë∂ÖÊó∂ÔºåËØ∑ÈáçÊñ∞ÈÄâÊã©',
              icon: 'none'
            })
            setIsExpanded(false)
            return 180
          }
          return prev - 1
        })
      }, 1000)
    } else {
      if (timerRef.current) {
        clearInterval(timerRef.current)
      }
    }

    return () => {
      if (timerRef.current) {
        clearInterval(timerRef.current)
      }
    }
  }, [hasItems, isExpanded])

  // Ê†ºÂºèÂåñÂÄíËÆ°Êó∂ÊòæÁ§∫
  const formatCountdown = (seconds: number) => {
    const mins = Math.floor(seconds / 60)
    const secs = seconds % 60
    return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`
  }

  // Ê†ºÂºèÂåñÊó•ÊúüÊòæÁ§∫
  const formatDate = (dateStr: string) => {
    const date = new Date(dateStr)
    const today = new Date()
    const isToday = date.toDateString() === today.toDateString()
    
    if (isToday) {
      return '‰ªäÂ§©'
    }
    
    const month = date.getMonth() + 1
    const day = date.getDate()
    return `${month}Êúà${day}Êó•`
  }

  const handleCheckoutClick = () => {
    if (!hasItems) {
      Taro.showToast({
        title: 'ËØ∑ÂÖàÈÄâÊã©ÊúçÂä°',
        icon: 'none'
      })
      return
    }
    
    setIsExpanded(true)
  }

  const handleMaskClick = () => {
    setIsExpanded(false)
  }

  const handleConfirmCheckout = () => {
    onCheckout()
  }

  return (
    <>
      {/* ÂçäÈÄèÊòéÈÅÆÁΩ© */}
      {isExpanded && (
        <View className="cart-mask" onClick={handleMaskClick} />
      )}

      {/* Â∫ïÈÉ®Âõ∫ÂÆöÊ†è */}
      <View className="shopping-cart">
        <View className="cart-bar">
          <View className="cart-info">
            {hasItems ? (
              <>
                <Text className="total-price">¬•{totalDiscountPrice}</Text>
                {totalSavings > 0 && (
                  <Text className="savings">Â∑≤‰ºòÊÉ†¬•{totalSavings}</Text>
                )}
              </>
            ) : (
              <Text className="empty-text">ËØ∑ÈÄâÊã©ÊúçÂä°È°πÁõÆ</Text>
            )}
          </View>
          
          <View 
            className={`checkout-btn ${!hasItems ? 'disabled' : ''}`} 
            onClick={handleCheckoutClick}
          >
            ÂéªÁªìÁÆó
          </View>
        </View>
      </View>

      {/* Â±ïÂºÄÁöÑË¥≠Áâ©ËΩ¶ËØ¶ÊÉÖ */}
      {isExpanded && (
        <View className="cart-expanded">
          <View className="expanded-header">
            <Text className="title">Â∑≤ÈÄâÊé®ÊãøÂ∏à({items.length})‰Ωç</Text>
            <Text className="action" onClick={handleMaskClick}>ÁªßÁª≠È¢ÑÁ∫¶</Text>
          </View>

          {/* ÊúçÂä°ËØ¶ÊÉÖ */}
          <View className="service-list">
            {items.map((item, index) => (
              <View key={index} className="service-item">
                <Image 
                  className="therapist-avatar" 
                  src={item.therapistAvatar || therapist?.avatar || ''} 
                />
                <View className="service-info">
                  <View className="info-header">
                    <Text className="therapist-name">{item.therapistName}</Text>
                    <Text className="duration">{item.duration}ÂàÜÈíü</Text>
                  </View>
                  <View className="info-detail">
                    <Text className="service-name">{item.serviceName}</Text>
                  </View>
                  <View className="info-time">
                    <Text className="time-text">
                      {formatDate(item.date)} {item.time} Ëá≥ {
                        // ËÆ°ÁÆóÁªìÊùüÊó∂Èó¥
                        (() => {
                          const [hour, minute] = item.time.split(':').map(Number)
                          const endMinute = minute + item.duration
                          const endHour = hour + Math.floor(endMinute / 60)
                          const finalMinute = endMinute % 60
                          return `${endHour}:${finalMinute.toString().padStart(2, '0')}`
                        })()
                      }
                    </Text>
                  </View>
                </View>
                <View className="price-info">
                  <Text className="price">¬•{item.discountPrice || item.price}</Text>
                </View>
              </View>
            ))}
          </View>

          {/* ÂèØÈÄâÂ¢ûÂÄºÈ°πÁõÆ */}
          <View className="addon-section">
            <Text className="section-title">ÂèØÈÄâÂ¢ûÂÄºÈ°πÁõÆ</Text>
            <View className="addon-list">
              <View className="addon-item">
                <View className="addon-info">
                  <Text className="addon-name">ÂàÆÁóß20ÂàÜÈíü</Text>
                  <Text className="addon-price">¬• 99</Text>
                </View>
                <View className="addon-action">+</View>
              </View>
              <View className="addon-item">
                <View className="addon-info">
                  <Text className="addon-name">Âä†Èíü20ÂàÜÈíü</Text>
                  <Text className="addon-price">¬• 99</Text>
                </View>
                <View className="addon-action">+</View>
              </View>
            </View>
          </View>

          {/* Â∫ïÈÉ®ÁªìÁÆó‰ø°ÊÅØ */}
          <View className="checkout-section">
            <View className="price-summary">
              <View className="cart-icon">
                <Text className="icon">üõí</Text>
                <View className="badge">1</View>
              </View>
              <View className="price-detail">
                <Text className="final-price">¬• {totalDiscountPrice}</Text>
                {totalOriginalPrice > totalDiscountPrice && (
                  <Text className="original-price">¬• {totalOriginalPrice}</Text>
                )}
              </View>
              <Text className="discount-tip">Â∑≤‰∫´ÂèóÊúÄÂ§ß‰ºòÊÉ†Âáè20ÂÖÉ</Text>
            </View>
            
            <View className="checkout-footer">
              <Text className="countdown">ÊîØ‰ªòÂÄíËÆ°Êó∂: {formatCountdown(countdown)}</Text>
              <View className="confirm-btn" onClick={handleConfirmCheckout}>
                ÂéªÁªìÁÆó
              </View>
            </View>
          </View>
        </View>
      )}
    </>
  )
}

export default ShoppingCart